/* ----------------------------------------------------------- */
.tingle-modal * {
  box-sizing: border-box; }

.tingle-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  visibility: hidden;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  background: rgba(0, 0, 0, 0.286);
  opacity: 0;
  cursor: pointer;
  transition: transform .2s ease; }

/* confirm and alerts
-------------------------------------------------------------- */
.tingle-modal--confirm .tingle-modal-box {
  text-align: center; }

/* modal
-------------------------------------------------------------- */
.tingle-modal--noOverlayClose {
  cursor: default; }

.tingle-modal--noClose .tingle-modal__close {
  display: none; }

.tingle-modal__close {
  position: fixed;
  top: 10px;
  right: 28px;
  z-index: 1000;
  padding: 0;
  width: 5rem;
  height: 5rem;
  border: none;
  background-color: transparent;
  color: #f0f0f0;
  font-size: 6rem;
  font-family: monospace;
  line-height: 1;
  cursor: pointer;
  transition: color .3s ease; }

.tingle-modal__closeLabel {
  display: none; }

.tingle-modal__close:hover {
  color: #fff; }

.tingle-modal-box {
  box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
  position: relative;
  flex-shrink: 0;
  margin-top: auto;
  margin-bottom: auto;
  min-width: 250px;
  max-width: 80vw;
  border-radius: 2px;
  background: #fff;
  opacity: 0;
  cursor: auto;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  transform: translateY(80px) scale(0.9); }

.tingle-modal-box__content {
  padding: 24px; }

.tingle-modal-box__footer {
  padding: 1.5rem 2rem;
  width: auto;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  background-color: #f5f5f5;
  cursor: auto; }

.tingle-modal-box__footer::after {
  display: table;
  clear: both;
  content: ""; }

.tingle-modal-box__footer--sticky {
  position: fixed;
  bottom: -200px;
  /* TODO : find a better way */
  z-index: 10001;
  opacity: 1;
  transition: bottom .3s ease-in-out .3s; }

/* state
-------------------------------------------------------------- */
.tingle-enabled {
  position: fixed;
  overflow: hidden;
  left: 0;
  right: 0; }

.tingle-modal--visible .tingle-modal-box__footer {
  bottom: 0; }

.tingle-enabled .tingle-content-wrapper {
  filter: blur(8px); }

.tingle-modal--visible {
  visibility: visible;
  opacity: 1; }

.tingle-modal--visible .tingle-modal-box {
  opacity: 1;
  transform: translateY(0) scale(1); }

.tingle-modal--overflow {
  overflow-y: scroll;
  padding-top: 8vh; }

/* btn
-------------------------------------------------------------- */
.tingle-btn {
  display: inline-block;
  margin: 0 .5rem;
  padding: 1rem 2rem;
  border: none;
  background-color: grey;
  box-shadow: none;
  color: #fff;
  vertical-align: middle;
  text-decoration: none;
  font-size: inherit;
  font-family: inherit;
  line-height: normal;
  cursor: pointer;
  transition: background-color .4s ease; }

.tingle-btn--primary {
  background-color: #3498db; }

.tingle-btn--danger {
  background-color: #e74c3c; }

.tingle-btn--default {
  background-color: #34495e; }

.tingle-btn--pull-left {
  float: left; }

.tingle-btn--pull-right {
  float: right; }

/* responsive
-------------------------------------------------------------- */
@media (max-width: 540px) {
  .tingle-modal {
    top: 0px;
    display: block;
    padding-top: 60px;
    width: 100%; }
  .tingle-modal-box {
    width: auto;
    border-radius: 0; }
  .tingle-modal-box__content {
    overflow-y: scroll; }
  .tingle-modal--noClose {
    top: 0; }
  .tingle-modal--noOverlayClose {
    padding-top: 0; }
  .tingle-modal-box__footer .tingle-btn {
    display: block;
    float: none;
    margin-bottom: 1rem;
    width: 100%; }
  .tingle-modal__close {
    top: 0;
    right: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 60px;
